////
/// Header
/// @group layout
////

.header {
    background-color: $color-black;
    color: $color-white;
    position: relative;
}

.header__wrapper {
    @include grid-top;
}

.header__notice {
    background-color: $color-gallery;
    border-bottom-width: 0;
    color: inherit;
    display: block;
    flex: 1 0 100%;
    left: 50%;
    line-height: $line-height-small;
    padding: $measure-small;
    position: relative;
    text-align: center;
    top: -$grid-padding;
    transform: translateX(-50%);
    width: 100%;

    span {
        border-bottom: $border-width-hairline $border-style-default $transparent;

        &:after {
            content: " ";
        }
    }

    &:hover,
    &:focus {
        span {
            border-bottom-color: currentColor;
        }
    }

    &:active {
        background-color: $color-white;
        color: contrasting-color($color-white);
    }
}

.header__logo {
    $header-logo-width: 110px;
    $header-logo-ratio: 0.683;
    @include grid;

    padding-bottom: $measure-medium;
    width: grid-column-width(6);
    align-self: center;

    @include grid-column-map(
        (
            "min": 6,
            "small": 6,
            "between": 4,
            "medium": 5,
            "large": 4,
            "extralarge": 4
        ),
        true,
        false
    );

    a,
    img {
        display: block;
    }

    img {
        background-color: $color-white;
        margin-left: $measure-small;
        padding: 36px 24px;
        width: $header-logo-width;
        height: $header-logo-width * (1/$header-logo-ratio);

        @include media("below:medium") {
            display: none;
        }
    }

    a {
        @include anchor("reset");
        @include anchor("block--dark-bg");

        font-family: $font-family-lato;
        font-size: $font-size-alpha;
        font-weight: $font-weight-heavy;
        margin-left: $measure-medium;
        padding-top: $measure-medium + $measure-small;
        align-self: center;

        span {
            color: $color-hubgrey;
            font-weight: $font-weight-default;

            &:hover,
            &:focus,
            &:active {
                color: darken($color-hubgrey, 6%);
            }
        }

        @include media("above:medium") {
            min-height: floor($header-logo-width * $header-logo-ratio);
        }

        @include media("below:medium") {
            font-size: $font-size-gamma;
        }

        @include media("below:min") {
            font-size: $font-size-delta;
        }
    }

}

.header__toggles {
    text-align: right;
}

.breadcrumbs-dropdown {
    display: none;
}
