////
/// Nav Mobile
/// @group components
////

.utility-links {
    @include grid;
    @include grid-column-map(
        (
            "between": 4,
            "medium": 3,
            "large": 4,
            "extralarge": 3
        )
    );
    left: grid-column-width(1);
    align-self: center;

    @include media("below:between") {
        display: none;
    }

    @include media("below:medium") {
        left: 0;
    }

    @include media("above:large") {
        left: grid-column-width(1);
    }

    .utility-links__list {
        @include grid;

        li {
            width: grid-column-width(4);

            padding-top: 0.75rem;
            text-align: center;
        }
    }

    span {
        display: block;

        &.fa {
            margin-bottom: $measure-small;
        }

        &:not(.fa) {
            font-family: $font-family-default;
            font-weight: $font-weight-semibold;
        }
    }

    a {
        @include anchor(reset);
        @include anchor(skate--dark-bg);

        padding: 0;
        text-transform: uppercase;
    }
}
