////
/// Fallout
/// @group layout
////

.fallout {
    @include grid;

    & > div {
        @include grid-column-map(
                        (
                                "small": 8,
                                "medium": 6
                        )
        );
    }

    aside {
        @include grid-column(3, "medium");
    }

    figure {
        clear: both;
    }
}

.fallout--none,
.fallout--both {
    aside {
        @include grid-column-map(
                        (
                                "small": 4,
                                "medium": 3
                        )
        );
    }
}

.fallout--none,
.fallout--right {
    & > div {
        @include grid-column-push-map(
                        (
                                "small": 4,
                                "medium": 3
                        )
        );
    }
}

.fallout--both,
.fallout--left {
    & > div {
        @include grid-column-push-map(
                        (
                                "small": 2,
                                "medium": 3
                        )
        );
    }
}

.fallout--both {
    @include fallout-figure-side(left);
    @include fallout-figure-side(right);
    @include fallout-figure-side(both);
}

.fallout--left {
    @include fallout-figure-side(left, true);
}

.fallout--right {
    @include fallout-figure-side(right, true);
}

.fallout-column {
    @include grid-column-map(
                    (
                            "small": 8,
                            "medium": 6
                    )
    );

    @include grid-column-push-map(
                    (
                            "small": 2,
                            "medium": 3
                    )
    );
}
