.ksp__items {
    justify-content: center;
}

.ksp__item {
    text-align: center;
    padding-top: $measure-large;
    padding-bottom: $measure-medium;
    border-bottom: 2px #f2f2f2 solid;

    @include grid-column-map(
                    (
                            "small": 6,
                            "medium": 3
                    )
    );

    .ksp__items--3 & {
        @include media("above:medium") {
            @include grid-column-map(
                            (
                                    "small": 6,
                                    "medium": 4
                            )
            );
        }
    }

    @include media("above:small") {
        border-bottom: none;
        border-right: 2px #f2f2f2 solid;

        &:last-child {
            border: none;
        }
    }

    @include media("below:medium") {
        &:nth-child(2) {
            border-right: none;
        }
    }
}

.ksp__item__figure {
    font-size: 70px;
    color: $color-alizarin;
    font-weight: $font-weight-thin;
    margin: 0;
    line-height: 1;
    white-space: nowrap;

    .animate--number__unit {
        font-size: 0.5em;
        display: inline-block;
        vertical-align: middle;
        padding: 0 ($measure-small / 2);
    }
}

.ksp__item__pre-figure {
    color: $color-alizarin;
    text-transform: uppercase;
    margin: 0;
    font-weight: $font-weight-semibold;
    position: absolute;
    top: $measure-small;
    left: 0;
    width: 100%;
}

.ksp__item__text {
    padding-top: 10px;
    color: $color-lucifer;
}

$level-colors: (
        undergraduate: $color-flamingo,
        masters: $color-prussian,
        mba: $color-sherpa
);

@each $level, $color in $level-colors {
    .ksp__item--#{$level} {
        .ksp__item__figure,
        .ksp__item__pre-figure {
            color: #{$color};
        }
    }
}
