////
/// Link strip
/// @group components
////


.link-strip {
    @include grid;
    text-align: center;
    padding-top: $measure-medium;

    & > div {
        display: inline-block;
        margin-bottom: $measure-large;
    }

    a {
        @include anchor(boxed-arrow);
    }

    @supports (display: flex) {
        justify-content: center;

        & > div {
            @include grid-column-base;

            @include media("above:large") {
                width: grid-column-width(2);
            }

            @include media("below:large", "above:medium") {
                width: grid-column-width(3);
            }

            @include media("below:medium", "above:small") {
                width: grid-column-width(4);
            }

            @include media("below:small") {
                width: grid-column-width(12);
                text-align: left;
            }
        }
    }
}
