////
/// Quick links
/// @group components
////

.quick-links {
    @include grid;
    padding-bottom: $measure-small;
    padding-left: $measure-medium + $measure-small;
    padding-right: $measure-medium + $measure-small;

    @include media("below:medium") {
        padding-left: $measure-medium + $measure-small;
        padding-right: $measure-medium + $measure-small;
    }

    .quick-links__list {
        @include grid-top;
    }

    .quick-link {
        margin-right: $measure-medium;
        padding-bottom: $measure-medium;
        width: 100%;

        @include media("above:between") {
            width: calc(#{50%} - #{$measure-medium});
        }

        @include media("above:medium") {
            width: calc(#{33%} - #{$measure-medium});
        }

        @include media("above:large") {
            width: calc(#{25%} - #{$measure-medium});
        }

        a {
            @include anchor(quick-link);
            font-weight: $font-weight-bold;
        }

        .fa {
            display: inline-block;
            padding-left: $measure-small;
            padding-right: $measure-medium;
            text-align: center;
            width: $measure-large;
        }
    }
}
