////
/// Induction timetable
/// @group components
////
.accordion--induction {
    .accordion__content__heading-anchor.active {
        background: $color-hub;
    }

    .accordion__content__heading-anchor__label {
        font-size: $font-size-gamma;
        font-weight: $font-weight-bold;
    }
}

.induction__events__event {
    box-shadow: 0 0 1.5rem 3px rgba(0,0,0,0.15);
    margin-bottom: $measure-medium;
    margin-right: $measure-large;
    padding: $measure-medium;

    @include media("below:medium") {
        margin-right: $measure-small;
    }

    .induction__events__details {
        @include grid;

        .induction__events__time,
        .induction__events__restriction {
            @include grid-column(6);
        }

        .induction__events__restriction {
            text-align: right;

            @include media("below:medium") {
                text-align: left;
            }
        }

        background: $color-hubsecondary;
        border-bottom: $border-width-default $border-style-default $color-black;
        color: $color-white;
        font-size: $font-size-delta;
        font-weight: $font-weight-semibold;
        margin-left: -#{$measure-medium};
        margin-right: -#{$measure-medium};
        margin-top: -#{$measure-medium};
        padding: $measure-small;
        width: calc(100% + #{$measure-large});
    }

    .induction__events__optional {
        background: $color-white-smoke;
        border-top: $border-width-default $border-style-default $color-quill;
        color: $color-black;
        font-size: $font-size-delta;
        font-weight: $font-weight-semibold;
        margin-left: -#{$measure-medium};
        margin-right: -#{$measure-medium};
        margin-bottom: -#{$measure-medium};
        padding: $measure-small;
        text-align: right;
        width: calc(100% + #{$measure-large});
    }

    .induction__events__facilitators {
        font-size: $font-size-small;
    }
}

.induction__header {
        @include grid;

        p {
            @include grid-column(6);
        }

        p + p {
            text-align: right;

            @include media("below:medium") {
                text-align: left;
            }

            span {
                font-weight: $font-weight-semibold;
            }
        }
}

.induction__events__facilitator-list {
    @include grid-column(12);

    border-top: $border-width-hairline $border-style-default $color-gallery;
}

.induction__event__attendance {
    @include grid;

    font-size: $font-size-delta;
    margin-bottom: $measure-medium;

    .induction__events__time,
    .induction__events__restriction {
        @include grid-column(6);
    }

    .induction__events__optional {
        font-weight: $font-weight-semibold;
    }

    .induction__events__restriction {
        font-weight: $font-weight-semibold;
        text-align: right;

        @include media("below:medium") {
            text-align: left;
        }
    }
}
