.cookie-notice {
    padding: 20px 0;
    background: rgba(0, 0, 0, 0.85);
    color: white;
    font-size: 15px;

    .home & {
        background: rgba(30, 30, 30, 0.90);
    }

    //@include section-white-links($background);
    a {
        &:link, &:visited {
            color: #eeeeee;
            border-bottom: 2px dotted #cccccc;
        }

        &:hover {
            color: white;
            border-bottom: 2px solid white;
        }

        &:focus, &:active {
            background-color: white;
            color: #555555;
            border-bottom: 2px solid white;
        }
    }
}

.cookie-notice__text {
    margin: 0 30px;

    @include media("above:small") {
        margin-right: 150px;
    }
}

.cookie-notice__inner {
    position: relative;
}

.cookie-notice__dismiss {
    display: none;

    position: relative;

    margin-left: 10px;

    button {
        border: none;
        padding: 10px 15px;
        text-align: left;
        cursor: pointer;

        color: white;
        background: #b91b1b;

        transition: color 100ms, background 100ms;

        &:hover {
            background: #eeeeee;
            color: #333333;
        }
        &:active, &:focus {
            background: #eeeeee;
            color: #b91b1b;
        }
    }

    margin-top: 20px;
    text-align: center;

    @include media("above:small") {
        position: absolute;
        top: 0;
        margin-top: 0;
        right: 30px;
    }
}

.js {
    .cookie-notice {
        display: none;
        position: fixed;
        z-index: 1000;
        width: 100%;
        bottom: 0;
        left: 0;
    }

    .cookie-notice__dismiss {
        display: block;
    }
}
