/**
 * Pagination
 */
.pagination {
    width: 100%;
    margin: 1rem 0;
    text-align: center;
    display: inline-block;
    list-style: outside none none;
    line-height: 100%;
    font-weight: 600;
}

.pagination .previous, .pagination .number, .pagination .after, .pagination .ellipsis {
    padding: 4px 8px;
    margin: 10px 0 10px 5px;
    background-color: #ececec;
}

.pagination .previous, .pagination .number, .pagination .after {
    border: 1px solid transparent;
}

.pagination .number, .pagination .ellipsis {
    display: none;
}

.pagination .selected {
    display: inline;
    margin: 10px 0 10px 5px;
    color: #666666;
    border: 1px solid transparent;
}

.pagination .selected:before {
    content: "Page ";
}

.pagination .previous:before {
    content: "< ";
}

.pagination .after:after {
    content: " >";
}

.pagination a {
    text-decoration: none;
    color: #0c0c0e;
    background-color: #ececec;
    border: 1px solid transparent;
    display: inline;
}

.pagination a:link {
    color: #0c0c0e;
}

.pagination a:visited {
    color: #0c0c0e;
}

.pagination a:hover {
    color: #33cbb4;
    background-color: #ececec;
    border: 1px solid #33cbb4;
}

.pagination a:active, .pagination a:focus {
    color: #0c0c0e;
    background-color: #33cbb4;
    border: 1px solid transparent;
}

.pagination .disabled {
    color: #666666;
    background-color: #ececec;
}

.pagination .ellipsis {
    background-color: transparent;
    margin: 0;
}

@media (min-width: 501px) {
    .pagination .ellipsis, .pagination .first-number, .pagination .last-number, .pagination .display {
        display: inline;
    }
    .pagination .number.extended {
        display: none;
    }
    .pagination .selected {
        border: 1px solid #666666;
    }
    .pagination .selected:before {
        content: "";
    }
    .pagination .previous:before {
        content: "";
    }
    .pagination .after:after {
        content: "";
    }
}

@media (min-width: 501px) {
    .pagination .number {
        display: inline;
    }
}

@media (min-width: 901px) {
    .pagination .number.extended {
        display: inline;
    }
}

