.standfirst {
    font-family: "Lato", "Helvetica Neue", Arial, Helvetica, sans-serif;
    color: #555555;
    font-size: 1.44rem;
    font-weight: 300;
    line-height: 1.5;
    text-align: center;
}

.footer-meta {
    position: relative;
    line-height: 1.5;
    width: 100%;
    align-self: flex-end;
}

.footer-meta .footer-meta__card .footer-meta__card__img img {
    width: 100%;
    margin: 0.5rem 0;
}

.footer-meta {
    position: relative;
}

.footer-meta:before {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    user-select: none;
    -webkit-touch-callout: default;
    -webkit-touch-callout: none;
    content: "";
    width: 200vw;
    height: 100%;
    pointer-events: none;
}

.lt-ie10 .footer-meta:before {
    left: 0;
    transform: none;
}

@media (max-width: 900px) {
    .footer-meta:before {
        max-width: calc(100% + 1rem);
    }
}

.footer-meta.section--hub:before {
    background-color: #ffffff;
}

.footer-meta.section--undergraduate:before {
    background-color: #f15f22;
}

.footer-meta.section--executive-education:before {
    background-color: #842b91;
}

.footer-meta.section--masters:before {
    background-color: #00284d;
}

.footer-meta.section--mba:before {
    background-color: #00444d;
}

.footer-meta.section--phd, .footer-meta.section--executive-phd {
    margin-top: 3.5rem;
}

.footer-meta.section--phd:before, .footer-meta.section--executive-phd:before {
    background-color: #0077a0;
}

.footer-meta.section--corporate:before {
    background-color: #f2f2f2;
}

.scp--hub .footer-meta.section--scp-theme:before {
    background-color: #ffffff;
}

.scp--corporate .footer-meta.section--scp-theme:before {
    background-color: #f2f2f2;
}

.scp--undergraduate .footer-meta.section--scp-theme:before {
    background-color: #f15f22;
}

.scp--masters .footer-meta.section--scp-theme:before {
    background-color: #00284d;
}

.scp--phd .footer-meta.section--scp-theme:before {
    background-color: #0077a0;
}

.scp--mba .footer-meta.section--scp-theme:before {
    background-color: #00444d;
}

.scp--executive-education .footer-meta.section--scp-theme:before {
    background-color: #842b91;
