    padding-left: 0.5rem;
    padding-right: 0.5rem;
    position: relative;
}

@media (max-width: 500px) {
    .footer-meta__card {
        margin-bottom: 1rem;
    }
}

@supports (display: flex) {
    .footer-meta__card {
        float: none;
        flex: 0 1 auto;
    }
}

@media (max-width: 500px) {
    .footer-meta__card {
        width: 100%;
    }
}

@media (min-width: 501px) {
    .footer-meta__card {
        width: 50%;
    }
