.footer-meta__card h5 {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    color: #666666;
}

.footer-meta__card a {
    vertical-align: top;
    word-wrap: break-word;
}

.footer-meta__card__wrapper {
    padding: 1rem;
    box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    background-color: #ffffff;
    height: 100%;
}

@media (max-width: 500px) {
    .footer-meta__card__wrapper {
        width: auto;
    }
}

@media (min-width: 501px) {
    .footer-meta__card__wrapper {
        width: 100%;
    }
