}

.footer-meta__card__social a:focus {
    outline: 2px dotted #0c0c0e;
    outline-offset: 3px;
}

.footer-meta__card__social a:active {
    outline: 2px dotted #0c0c0e;
    outline-offset: 0;
}

.footer-meta__card__social .footer-meta__card__social__icons {
    display: inline;
