.socialmedia-card__logo {
    font-size: 1.44rem;
    color: #646464;
    text-align: center;
    line-height: 1;
    margin-bottom: 1rem;
}

.socialmedia-card__title {
    color: #646464;
    line-height: 1.375;
    margin-bottom: 0.5rem;
}

.socialmedia-card__title a {
    font-weight: 700;
}

.socialmedia-card__title svg {
    display: inline-block;
    vertical-align: bottom;
    margin-right: 0.5rem;
    fill: #646464;
    height: 20px;
}

.socialmedia-card__message {
    color: #0c0c0e;
    line-height: 1.375;
    word-wrap: break-word;
    margin-bottom: 0.5rem;
}

.socialmedia-card__item-url {
    line-height: 1.375;
    word-wrap: break-word;
    margin-bottom: 1rem;
}

.socialmedia-card__timestamp,
.socialmedia-card__actions {
    display: inline-block;
    color: #646464;
    font-size: 14px;
    line-height: 1.375;
    margin-bottom: 0.5rem;
}

.socialmedia-card__timestamp {
    margin-right: 1rem;
}

.socialmedia-card__timestamp:after {
    content: "\2219";
    font-weight: 700;
    margin-left: 1rem;
}

