    background-color: inherit;
    color: inherit;
    border-bottom-width: 0;
    margin-right: 0.75rem;
}

.socialmedia-card__actions a:hover, .socialmedia-card__actions a:focus, .socialmedia-card__actions a:active {
    background-color: inherit;
    color: inherit;
    outline-width: 0;
}

.socialmedia-card__actions a:hover {
    filter: brightness(85%);
}

.socialmedia-card__actions a:focus {
    outline: 3px solid #0c0c0e;
    outline-offset: 3px;
}

.socialmedia-card__actions a:active {
    outline: 2px dotted #0c0c0e;
    outline-offset: 0;
}

