.chart-row__items__chart__row__legend__item {
    margin-bottom: 0.5rem;
    font-family: "Lato", "Helvetica Neue", Arial, Helvetica, sans-serif;
    font-weight: 700;
    font-size: 1.2rem;
}

.chart-row__items__chart__row__legend__item:not(:last-child) {
    padding-bottom: 0.5rem;
    position: relative;
}

.chart-row__items__chart__row__legend__item:not(:last-child):after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 1rem;
    background: #d0cfcb;
    height: 1px;
    width: 50%;
}

.chart-row__items__chart__row__legend__item__label {
    margin-left: 1rem;
    color: #555555;
    font-size: 1rem;
    font-weight: 400;
}

table {
    overflow: hidden;
    position: relative;
    z-index: 0;
    width: auto;
    max-width: 100%;
    margin: 0.5rem auto;
    border-collapse: collapse;
}

table td,
table th {
    padding: 10px;
    position: relative;
    outline: 0;
    text-align: center;
    border-bottom: 1px solid #e5e5e5;
    vertical-align: top;
    border-left: 0;
    border-right: 0;
    margin: 0;
}

table th {
    font-weight: 700;
}

table tr {
    position: relative;
    border-left: 0;
    border-right: 0;
    margin: 0;
    padding: 0;
}

table thead th {
    border-bottom-width: 2px;
}

table tbody th {
    text-align: left;
    white-space: nowrap;
}

table tbody > tr:hover td,
table tbody > tr:hover th {
    background: #f2f2f2;
}

table td:hover:after,
table thead th:not(:empty):hover:after {
    content: "";
    position: absolute;
    z-index: -2;
    top: -5000px;
    left: 0;
    width: 100%;
    height: 10000px;
    background: #f2f2f2;
}

table tbody > tr > td:hover {
    background: #d8d8d8;
}

.news__img {
    margin: 0 auto 1rem;
}

.news__published {
    font-style: italic;
    margin-bottom: 1rem;
}

.student-news-finder {
    margin-top: -2rem;
}

.student-news-finder .student-news-finder__wrapper {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    clear: both;
    zoom: 1;
    float: left;
    width: calc(100% + 1rem);
    margin-left: -0.5rem;
    margin-right: -0.5rem;
    min-height: 15.5rem;
}

.student-news-finder .student-news-finder__wrapper:before, .student-news-finder .student-news-finder__wrapper:after {
    content: "";
    display: block;
}

.student-news-finder .student-news-finder__wrapper:after {
    clear: both;
}

@supports (display: flex) {
    .student-news-finder .student-news-finder__wrapper {
        float: none;
    }
}

.student-news-finder {
    overflow-x: hidden;
}

.student-news-finder .student-news-finder__search-form {
    /** TODO: Is min height necessary? **/
    float: left;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    position: relative;
}

@supports (display: flex) {
    .student-news-finder .student-news-finder__search-form {
        float: none;
        flex: 0 1 auto;
    }
}

@media (max-width: 900px) {
    .student-news-finder .student-news-finder__search-form {
        width: 100%;
    }
}

@media (min-width: 901px) {
    .student-news-finder .student-news-finder__search-form {
        width: 50%;
    }
}

@media (max-width: 900px) {
    .student-news-finder .student-news-finder__search-form {
        left: 0;
    }
}

@media (min-width: 901px) {
    .student-news-finder .student-news-finder__search-form {
        left: 25%;
    }
}

@media (max-width: 1200px) {
    .student-news-finder .student-news-finder__search-form {
        /** TODO: Is min height necessary? **/
        float: left;
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        position: relative;
    }
    @supports (display: flex) {
        .student-news-finder .student-news-finder__search-form {
            float: none;
            flex: 0 1 auto;
        }
    }
}

@media (max-width: 1200px) and (max-width: 900px) {
    .student-news-finder .student-news-finder__search-form {
        width: 100%;
    }
}

@media (max-width: 1200px) and (min-width: 901px) {
    .student-news-finder .student-news-finder__search-form {
        width: 83.333%;
    }
