///
// Course Search
// @group components
///


.course-search {
    margin: $measure-large 0;
}

.course-search__title {
    @extend %gamma !optional;
    font-weight: $font-weight-light;
    text-align: center;

    span {
        @include media("below:medium") {
            display: none;
        }
    }
}

.course-search__form {
    width: 100%;
    display: flex;
    padding-bottom: $measure-medium;
    border-bottom: $border-width-thick $border-style-default $color-alizarin;
    margin: $measure-large 0;
    position: relative;
}

.course-search__form__select {
    flex: 1 1 auto;
    display: flex;
    overflow: hidden;
    position: relative;

    &:before {
        @include non-selectable;
        content: "\f0d7";
        color: $color-white;
        display: inline-block;
        position: absolute;
        top: 50%;
        right: $measure-medium;
        transform: translateY(-50%);
        font-family: $font-family-icon;
        font-size: $font-size-delta;
        pointer-events: none;
        z-index: z-index(above);
    }

    label {
        color: $color-black;
        padding-right: $measure-small;
        font-size: $font-size-delta;
        line-height: #{3 / strip-units($font-size-delta)};
    }

    select {
        @include anchor(block);
        appearance: none;
        background-color: $color-alizarin;
        color: contrasting-color($color-alizarin);
        flex: 1 0 auto;
        padding: 0 #{$measure-large + $measure-small} 0 $measure-medium;
        border-width: 0;
        border-radius: 0;
        position: relative;
        font-size: $font-size-default;
        font-weight: $font-weight-bold;
        line-height: 3;
    }
}

.course-search__form__text {
    @include transition(color);
    color: $color-lucifer;
    flex: 1 0 auto;
    padding: #{(3 - (strip-units($font-size-delta) * $line-height-default)) / 2 / strip-units($font-size-delta) * 1rem} $measure-small #{(3 - (strip-units($font-size-delta) * $line-height-default)) / 2 / strip-units($font-size-delta) * 1rem} $measure-medium;
    border-width: 0;
    font-size: $font-size-delta;
    line-height: $line-height-default;

    &:valid,
    &:focus {
        color: $color-black;
        outline: none;
    }
}

.course-search__form__submit {
    min-width: 3rem;
    color: $color-black;
    flex: 0 1 3rem;
    padding: 0 $measure-small;
    border: 0;
    font-family: $font-family-icon;
    font-size: $font-size-gamma;

    &:before {
        content: "\f002";
    }
}


$level-colors: (
    undergraduate: ($color-flamingo, $color-black),
    masters:       ($color-prussian, $color-white),
    mba:           ($color-sherpa,   $color-white),
    executive-education:        ($color-vivid,   $color-white),
);

@each $level, $colors in $level-colors {
    $background-color: nth($colors, 1);
    $text-color: nth($colors, 2);

    .course-search__form--#{$level} {
        border-bottom-color: $background-color;

        .course-search__form__select {
            &:before {
                color: $text-color;
            }

            select {
                background-color: $background-color;
                color: $text-color;
            }
        }
    }
}
