$kis-widget-horizontal-width: 615px;
$kis-widget-vertical-width: 190px;

.kis-widget {
    position: relative;

    @supports (display: flex) {
        display: flex;
        justify-content: center;
    }

    iframe {
        position: relative;
        left: 50%;
        display: block;
        transform: translateX(-50%);

        @supports (display: flex) {
            left: 0;
            transform: none;
        }
    }
}
