
$cutout-height: 4rem;

.banner--image {
    .banner__container,
    .banner__content {
        height: 100%;
    }

    .banner__content__text {
        margin-bottom: 3 * $measure-large;
    }

    &:not(.banner--with-ctas):not(.banner--image--with-promo):not(.banner--image--with-freehtml) .banner__content__text {
        margin-bottom: 5 * $measure-large;
    }

    .banner__content__text__standfirst {
        color: $color-white;
    }

    .banner__content__ctas {
        text-align: center;

        a {
            @include anchor(draw-outline, $color-white);
            margin: 0 $measure-medium $measure-large;
        }
    }

    &.banner--image--with-promo .banner__content__ctas {
        @include media("above:medium") {
            text-align: left;
        }
        @include media("above:large") {
            text-align: center;
        }
    }

    @include media("below:medium") {
        margin-bottom: $measure-large;
    }

    @include media("above:medium") {
        &.banner--cutout--left,
        &.banner--cutout--right {
            .banner__content {
                margin-bottom: $cutout-height;
            }
            .banner__content__ctas {
                bottom: 3rem;
            }

            .banner__standfirst {
                margin-top: -$cutout-height / 2;
                position: relative;
                z-index: z-index(above);
            }
        }
    }
}

.banner__image__group {
    @extend .img-bg-section;

    @include media("above:medium") {
        .banner--cutout--left &:after,
        .banner--cutout--right &:after {
            content: "";
            background-color: $color-white;
            width: calc(100% - #{$cutout-height});
            height: $cutout-height;
            position: absolute;
            bottom: 0;
            z-index: z-index(above);
        }

        .banner--cutout--left &:after {
            left: 0;
        }

        .banner--cutout--right &:after {
            right: 0;
        }
    }
}

.banner__img {
    height: 100%;
    >img {
        @extend .img-bg-section__img;
    }

    .banner--full-overlay &,
    .banner--partial-overlay & {
        &:after {
            display: block;
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: black;
            opacity: 0.8
        }
    }

    .banner--partial-overlay & {
        &:after {
            background: linear-gradient(0deg, $color-black, 70%, transparent) no-repeat bottom;
            background-size: 100% 70%;
        }
    }
}

.banner__img__none {
    background-color: $color-hubsecondary;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    min-width: 100%;
    max-width: none;
    min-height: 100%;
    width: calc(100% + #{$measure-medium});
}

.banner__over-image--no-img-bg {
    .banner__content__text {
        margin-bottom: $measure-medium !important;
    }
}

.banner--cutout--none {
    margin-bottom: $measure-large;
}
