////
/// Wide Section
/// @group layout
////

.wide-column {
    @include grid-column-map(
                    (
                            "small": 12,
                            "medium": 10,
                            "large": 8
                    )
    );

    @include grid-column-push-map(
                    (
                            "small": 0,
                            "medium": 1,
                            "large": 2
                    )
    );
}

.grid--tencol {
    @extend .grid;

    > div {
        @include grid-column-map(
                        (
                                "small": 12,
                                "large": 10
                        )
        );

        @include grid-column-push-map(
                        (
                                "small": 0,
                                "large": 1
                        )
        );
    }
}

.grid--side-by-side {
    @extend .grid;

    > div {
        @include grid-column-map(
                        (
                                "small": 12,
                                "between": 10,
                                "medium": 8,
                                "large": 6
                        )
        );

        @include grid-column-push-map(
                        (
                                "small": 0,
                                "between": 1,
                                "medium": 2,
                                "large": 0
                        )
        );
    }
}

.grid--thirds--small {
    @extend .grid;

    > div {
        @include grid-column(4, "small");
    }
}
