////
/// Nav Mobile
/// @group components
////

.nav-mobile {
    @include grid-column-base;
    padding-bottom: $measure-small;
    padding-top: $measure-medium;
    left: grid-column-width(4);
    width: grid-column-width(1);
    align-self: center;

    @include media("above:min") {
        left: grid-column-width(4);
    }

    @include media("above:small") {
        left: grid-column-width(3);
    }

    @include media("above:between") {
        left: grid-column-width(2);
        padding-bottom: $measure-small;
        padding-top: $measure-medium;
        width: grid-column-width(1);
    }

    @include media("above:medium") {
        left: grid-column-width(2);
    }

    @include media("above:extralarge") {
        left: grid-column-width(3);
    }
}

.nav-mobile__close-link {
    @include anchor(reset);
    @include anchor(nav-mobile-link);
}

.nav-mobile__close-link {
    @include absolute(top-right);
    height: 100%;
}

.nav-mobile__close-link {
    span {
        @extend .fa-close;
    }
}


.nav-mobile__toggle {
    display: none;
}

.nav-mobile__content {
    background-color: $color-black;
    color: $color-white;
    height: 100%;
    min-width: $fixed-sidebar-width;
    width: 50%;
    @include media("above:medium") {
        max-width: grid-column-width(4);
    }
    @include media("above:large") {
        max-width: grid-column-width(3);
    }

    z-index: z-index(navigation);

    .nav-mobile__toggle:not(:checked) + .nav-mobile__overlay & {
        @include visually-hidden;
    }

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        @include animation(false, nav-mobile-in);
    }
}

@keyframes nav-mobile-in {
    0% {
        opacity: 0;
        visibility: hidden;
    }
    0.001% {
        opacity: 1;
        visibility: visible;
        transform: translateX(100%);
    }
    100% {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
    }
}

.nav-mobile__toggle-label {
    @include non-selectable;
    background-color: $color-black;
    align-self: flex-end;
    cursor: pointer;
    display: inline-block;
    padding-top: $measure-small;
    padding-bottom: $measure-small;
    border-width: 0;
    vertical-align: top;
    line-height: $font-size-gamma;
    font-size: $font-size-gamma;
    font-weight: $font-weight-semibold;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;

    &:before {
        @include font-family(icon);
        content: "#{$fa-var-bars} ";
        font-weight: $font-weight-default;
    }

    &:focus {
        outline: $border-width-default $border-style-alternate $color-white;
        outline-offset: 0;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-white;
        outline-offset: $border-width-outline;
    }

    .closed {
        display: inline;
    }

    .opened {
        display: none;
    }

    &.open {
        background-color: $color-hub;
        color: $color-black;

        &:before {
            content: "#{$fa-var-close} ";
        }

        .opened {
            display: inline;
        }

        .closed {
            display: none;
        }

        &:hover {
            background-color: darken($color-hub, 6%);
            color: $color-black;
        }
    }

    &:not([class*="open"]) {
        @include anchor(outline--dark-bg);
    }

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        padding: $measure-small;
        position: fixed;
        top: 0;
        right: $measure-medium;
        z-index: z-index(modal);
    }

    .closed,
    .open {
        @include media("below:min") {
            display: none;
        }
    }
}

.nav-mobile__close-label {
    background-color: rgba(12, 12, 14, 0.7);
    width: 100%;
    height: 100%;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: z-index(above);

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        display: block;
    }
}

.nav-mobile__tertiary {
    background-color: $color-black;
    padding-top: $measure-small;
}

/** open state **/

.nav-mobile__overlay {
    .nav-mobile__toggle:checked + & {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        position: fixed;
        top: 0;
        right: 0;
        z-index: z-index(navigation);
    }
}

.nav-mobile__container {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        position: absolute;
        top: 0;
        right: 0;
        z-index: z-index(navigation);
    }
}
