////
/// Grid Mixins
/// @group abstracts
////

///
/// Grid Base
///
@mixin grid-base() {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

///
/// Top-Level Grid (Grandmaster Grid)
///
@mixin grid-top() {
    @include grid-base;
    @include clear;
    max-width: $grid-width-extra-large;

    @include media("below:small") {
        padding-left: $grid-padding-half;
        padding-right: $grid-padding-half;
    }

    @include media("above:small") {
        padding-left: $grid-padding;
        padding-right: $grid-padding;
    }
}

///
/// Grid
///
@mixin grid() {
    @include grid-base;
    @include clear;
    float: left;
    width: calc(100% + #{$grid-padding});
    margin-left: -$grid-padding-half;
    margin-right: -$grid-padding-half;

    @supports (display: flex) {
        float: none;
    }
}

///
/// Grid Column Base
///
@mixin grid-column-base() {
    /** TODO: Is min height necessary? **/
    //min-height: 1px;

    float: left;
    padding-left: $grid-padding-half;
    padding-right: $grid-padding-half;
    position: relative;

    @supports (display: flex) {
        float: none;
        flex: 0 1 auto;
    }
}

///
/// Grid Column Width
///
/// @author Chris Burnell
///
/// @param {Number} $value - Size/span of the column
///
/// @throw Error if not parameter is passed.
/// @throw Error if value is less than or equal to $grid-columns
///
@mixin grid-column($column-value, $transition-point: "medium", $include-grid-column: true, $include-full-width-declaration: true) {
    @if not $column-value {
        @error "grid-column() expects at least one parameter.";
    }

    $column-value: floor($column-value);

    @if not map-has-key($breakpoints, $transition-point) {
        @error "There is no `transition-point` named `#{$transition-point}` in `$breakpoints`. `transition-point` should be one of `#{map-keys($breakpoints)}`.";
    }

    @if ($column-value < 1) or ($column-value > $grid-columns) {
        @error "`#{$column-value}` must be greater than zero and less than or equal to `$grid-columns`.";
    }

    @if $include-grid-column {
        @include grid-column-base;
    }

    @if $include-full-width-declaration {
        @include media("below:#{$transition-point}") {
            width: 100%;
        }
    }

    @include media("above:#{$transition-point}") {
        width: grid-column-width($column-value);
    }
}

///
/// Grid Column Map
///
@mixin grid-column-map($map, $include-grid-column: true, $include-full-width-declaration: true) {
    @if not $map {
        @error "grid-column-map() expects at least one parameter.";
    }

    $transition-point-first: nth(map-keys($map), 1);

    @each $transition-point, $column-value in $map {
        @include grid-column($column-value, $transition-point, if($include-grid-column and $transition-point == $transition-point-first, true, false), if($include-full-width-declaration and $transition-point == $transition-point-first, true, false));
    }
}

///
/// Grid Column Push
///
@mixin grid-column-push($column-value, $transition-point: "medium", $include-zero-declaration: true) {
    @if not $column-value {
        @error "grid-column-push() expects at least one parameter.";
    }

    @if not map-has-key($breakpoints, $transition-point) {
        @error "There is no `transition-point` named `#{$transition-point}` in `$breakpoints`. `transition-point` should be one of `#{map-keys($breakpoints)}`.";
    }

    @if $include-zero-declaration {
        @include media("below:#{$transition-point}") {
            left: 0;
        }
    }

    @include media("above:#{$transition-point}") {
        left: grid-column-width($column-value);
    }
}

///
/// Grid Column Push Map
///
@mixin grid-column-push-map($map, $include-zero-declaration: true) {
    @if not $map {
        @error "grid-column-push-map() expects at least one parameter.";
    }

    $transition-point-first: nth(map-keys($map), 1);

    @each $transition-point, $column-value in $map {
        @include grid-column-push($column-value, $transition-point, if($include-zero-declaration and $transition-point == $transition-point-first, true, false));
    }
}

///
/// Grid Column Pull
///
@mixin grid-column-pull($column-value, $transition-point: "medium", $include-zero-declaration: true) {
    @if not $column-value {
        @error "grid-column-pull() expects at least one parameter.";
    }

    @if not map-has-key($breakpoints, $transition-point) {
        @error "There is no `transition-point` named `#{$transition-point}` in `$breakpoints`. `transition-point` should be one of `#{map-keys($breakpoints)}`.";
    }

    @if $include-zero-declaration {
        @include media("below:#{$transition-point}") {
            left: 0;
        }
    }

    @include media("above:#{$transition-point}") {
        left: #{grid-column-width($column-value) * -1};
    }
}

///
/// Grid Column Pull Map
///
@mixin grid-column-pull-map($map, $include-zero-declaration: true) {
    @if not $map {
        @error "grid-column-pull-map() expects at least one parameter.";
    }

    $transition-point-first: nth(map-keys($map), 1);

    @each $transition-point, $column-value in $map {
        @include grid-column-pull($column-value, $transition-point, if($include-zero-declaration and $transition-point == $transition-point-first, true, false));
    }
}

///
/// Fallout Figure Side
///
/// @param {String} $side - Side of the page on which to put the figure
/// @param {Boolean} $fall-any - Whether or not to target `any`
///
@mixin fallout-figure-side($side, $fall-any: false) {
    @if not $side {
        @error "fallout-figure-side() expects at least one parameter.";
    }

    @if not index($fallouts, $side) {
        @error "There is no Side named `#{$side}` in `$fallouts`. Side should be one of `#{map-keys($fallouts)}`.";
    }

    $base-width: 80%;

    $isBoth: $side == "both";

    $selector: if($fall-any, ".fallout__#{$side}, .fallout__any", ".fallout__#{$side}");

    $main-margin-side: if($side == "right", right, left);
    $other-margin-side: if($main-margin-side == "left", right, left);

    $main-margin-amount: -$base-width * if($isBoth, 0.25, 0.5);
    $other-margin-amount: if($isBoth, $main-margin-amount, $measure-medium);

    @include media("above:small") {
        #{$selector} {
            max-width: if($isBoth, 100% + $base-width / 4, $base-width);
            margin-#{$main-margin-side}: ($main-margin-amount / 2);
            margin-#{$other-margin-side}: ($other-margin-amount / 2);
            @if (not $isBoth) {
                float: $side;
            }
        }
    }

    @include media("above:medium") {
        #{$selector} {
            max-width: if($isBoth, 100% + $base-width / 2, $base-width);
            margin-#{$main-margin-side}: ($main-margin-amount / 2);
            margin-#{$other-margin-side}: ($other-margin-amount / 2);
            @if (not $isBoth) {
                float: $side;
            }
        }
    }
}
