////
/// Anchors Mixins
/// @group abstracts
////

/**
 * Managing anchors is a difficult process on a website as vast as ours, and
 * given the great variety in styles our anchors take, it is of the utmost
 * importance that they be set up properly and with extensibility on the mind.
 * The current issue we face is that we are not making proper use of the
 * cascade. New styles for links are introduced in a bespoke manner, and due to
 * the way these different designs and patterns are communicated, or lack
 * thereof, we even have instances where the same stylistic goal is being
 * accomplished in two or more separate instances of CSS. To properly use the
 * cascade, we need to dissect the variety of different styles we currently
 * use, figure out any common denominators and determine how each style
 * branches away from the default elements stylesexposing how each style
 * cascade and iterate on the established patterns.
 *
 * The biggest challenge in doing this is understanding the scope of the links
 * and how far-reaching any high-level styles will cascade, and mentally
 * extrapolating to understand how the styles will be managed and maintained,
 * and ensuring that every step of the way takes us closer to unity and
 * simplicity.
 *
 *
 * It is important to make the details of the anchor tag, its states, and its
 * functionality and known and understood completely:
 *
 * :link
 *   - Applies to any `<a>` tag with a `href` attribute, whether empty or not
 *   - Persists regardless of other states based on above criteria
 * :visited
 *   - Applies to any `<a>` tag with a `href` in the browser history
 *   - Applies to any `<a>` tag with a target anchor which has been activated
 *   - Restricted to styling `color`, `background-color`, `border-color` and
 *     variants, `outline-color`, and `fill` and `stroke` color values for
 *     security/privacy reasons
 *   - Similarly to above, checking for these properties with JS will yield
 *     results as if `:visited` anchors are just `:link` anchors
 * :hover
 *   - Applies to any `<a>` tag which the user designates with a pointing
 *     device but does not necessarily activate it
 * :focus
 *   - Applies to any `<a>` tag which has been activated by the user, either
 *     from selecting with they keyboard or pointing device
 * :active
 *   - Applies to any `<a>` tag which is *currently* being activated by the
 *     user (i.e. mouse button down)
 *
 *
 * Order of states for a link when it is moused over and clicked:
 *
 * default ->    :hover   -> :hover:focus -> :hover:active
 *   N/A   ->  Mouse Over ->  Mouse Down  ->   Mouse Up
 *
 * For this reason, its important to maintain an order of states in the CSS,
 * easily memorised with the phrase "LoVe HAte":
 *   - :link (unused)
 *   - :visited (unused)
 *   - :hover
 *   - :focus
 *   - :active
 *
 *
 * The only properties which live inside the `:link`, `:visited`, `:hover`,
 * `:focus`, and `:active` pseudo classes are as follow:
 *   - background-color
 *   - color
 *   - border-bottom-width
 *   - border-bottom-style
 *   - border-bottom-color
 *   - outline
 *   - text-decoration
 *
 *
 * It is important to take notice of some other key points regarding anchor
 * states:
 *   - `:focus` and `:active` states do not automatically trigger `:hover`;
 *     this means styles that should apply to all three must be declared for
 *     all three
 *
 *
 * If a link should *not* appear visually-similar to the default anchor styles,
 * there are a number of properties/values that need to be reset/unset which
 * cascade and need to be anticipated.
 *   1. Set the `border-border-width` to `0`
 *   2. Set the `background-color` to `transparent` for `:hover` state
 */

$anchor-types: (
        "activable-home-featured--hub",
        "activable-inline-featured",
        "activable-inline-featured--alizarin",
        "activable-inline-featured--prussian",
        "activable-inline-normal",
        "activable-inline-normal--air",
        "activable-inline-normal--allports",
        "activable-stacked-arrow",
        "activable-stacked-featured",
        "activable-stacked-normal",
        "activable-stacked-normal--allports",
        "activable-stacked-normal--transparent",
        "activable-stacked-simple",
        "announcement",
        "black",
        "block",
        "block--dark-bg",
        "boxed-arrow",
        "breadcrumb",
        "card",
        "cta",
        "cta--allports",
        "cta--clear-light",
        "cta--gtfo",
        "cta--hub",
        "cta--hub--borderless",
        "cta--hubgrey",
        "cta--hubsecondary",
        "cta--inline",
        "cta--montecarlo",
        "cta--neptune",
        "cta--orange",
        "cta--picton",
        "cta--pink",
        "cta--pita",
        "cta--prussian",
        "cta--purple",
        "cta--red",
        "cta--shadow",
        "cta--sherpa",
        "cta--small",
        "cta--white",
        "cta--yellow",
        "dark-background",
        "draw-outline",
        "external-img",
        "ghost",
        "hallway",
        "heading",
        "indicator",
        "inline",
        "inline-block",
        "left-arrow",
        "left-hand-navigation",
        "monochrome",
        "nav-mobile-link",
        "outline",
        "outline--dark-bg",
        "primary-navigation",
        "purple",
        "quick-link",
        "quick-link--feature",
        "railtrack",
        "reset",
        "right-arrow",
        "search-result",
        "secondary-navigation",
        "simple-image",
        "skate",
        "skate--block",
        "skate--dark-bg",
        "skip",
        "sliding--left",
        "sliding--right",
        "tertiary-navigation",
        "video-preview",
        "white"
) !default;

///
/// Anchor Styles
///
/// @author Chris Burnell
///
/// @param {String} $type - Anchor Type
/// @param {String} $border-radius - Type of Border Radius
///
/// @require {List} $anchor-types
///
@mixin anchor($type, $args...) {
    @if not $type {
        @error "anchor() expects an Anchor Type.";
    } @else if not index($anchor-types, $type) {
        @error "There is no Anchor Type of `#{$type}`. `type` should be one of `#{$anchor-types}`.";
    } @else if ($type == "reset") {
        @include anchor-reset;
    } @else if ($type == "block") {
        @include anchor-block;
    } @else if ($type == "block--dark-bg") {
        @include anchor-block--dark-bg;
    } @else if ($type == "inline") {
        @include anchor-inline;
    } @else if ($type == "inline-block") {
        @include anchor-inline-block;
    } @else if ($type == "outline") {
        @include anchor-outline;
    } @else if ($type == "outline--dark-bg") {
        @include anchor-outline--dark-bg;
    } @else if ($type == "dark-background") {
        @include anchor-dark-background;
    } @else if ($type == "heading") {
        @include anchor-heading;
    } @else if ($type == "card") {
        @include anchor-card;
    } @else if ($type == "cta") {
        @include anchor-cta;
    } @else if ($type == "cta--small") {
        @include anchor-cta--small;
    } @else if ($type == "cta--inline") {
        @include anchor-cta--inline;
    } @else if ($type == "cta--yellow") {
        @include anchor-cta--yellow;
    } @else if ($type == "cta--orange") {
        @include anchor-cta--orange;
    } @else if ($type == "cta--allports") {
        @include anchor-cta--allports;
    } @else if ($type == "cta--prussian") {
        @include anchor-cta--prussian;
    } @else if ($type == "cta--red") {
        @include anchor-cta--red;
    } @else if ($type == "cta--purple") {
        @include anchor-cta--purple;
    } @else if ($type == "cta--black") {
        @include anchor-cta--black;
    } @else if ($type == "cta--pink") {
        @include anchor-cta--pink;
    } @else if ($type == "cta--montecarlo") {
        @include anchor-cta--montecarlo;
    } @else if ($type == "cta--picton") {
        @include anchor-cta--picton;
    } @else if ($type == "cta--sherpa") {
        @include anchor-cta--sherpa;
    } @else if ($type == "cta--neptune") {
        @include anchor-cta--neptune;
    } @else if ($type == "cta--white") {
        @include anchor-cta--white;
    } @else if ($type == "cta--shadow") {
        @include anchor-cta--shadow;
    } @else if ($type == "cta--gtfo") {
        @include anchor-cta--gtfo;
    } @else if ($type == "cta--pita") {
        @include anchor-cta--pita;
    } @else if ($type == "cta--hub") {
        @include anchor-cta--hub;
    } @else if ($type == "cta--hubsecondary") {
        @include anchor-cta--hubsecondary;
    } @else if ($type == "cta--hub--borderless") {
        @include anchor-cta--hub--borderless;
    } @else if ($type == "cta--hubgrey") {
        @include anchor-cta--hubgrey;
    } @else if ($type == "cta--clear-light") {
        @include anchor-cta--clear-light;
    } @else if ($type == "left-arrow") {
        @include anchor-left-arrow;
    } @else if ($type == "right-arrow") {
        @include anchor-right-arrow;
    } @else if ($type == "purple") {
        @include anchor-purple;
    } @else if ($type == "black") {
        @include anchor-black;
    } @else if ($type == "indicator") {
        @include anchor-indicator;
    } @else if ($type == "activable-stacked-normal") {
        @include anchor-activable-stacked-normal;
    } @else if ($type == "activable-stacked-normal--allports") {
        @include anchor-activable-stacked-normal($color-allports);
    } @else if ($type == "activable-stacked-normal--transparent") {
        @include anchor-activable-stacked-normal($transparent);
    } @else if ($type == "activable-stacked-simple") {
        @include anchor-activable-stacked-simple;
    } @else if ($type == "activable-stacked-featured") {
        @include anchor-activable-stacked-featured;
    } @else if ($type == "activable-stacked-arrow") {
        @include anchor-activable-stacked-arrow;
    } @else if ($type == "activable-inline-normal") {
        @include anchor-activable-inline-normal;
    } @else if ($type == "activable-inline-normal--allports") {
        @include anchor-activable-inline-normal($color-allports);
    } @else if ($type == "activable-inline-normal--air") {
        @include anchor-activable-inline-air;
    } @else if ($type == "activable-inline-featured") {
        @include anchor-activable-inline-featured;
    } @else if ($type == "activable-inline-featured--prussian") {
        @include anchor-activable-inline-featured($color-prussian);
    } @else if ($type == "activable-inline-featured--alizarin") {
        @include anchor-activable-inline-featured($color-alizarin);
    } @else if ($type == "activable-home-featured--hub") {
        @include anchor-activable-home-featured($color-hub);
    } @else if ($type == "quick-link") {
        @include anchor-quick-link;
    } @else if ($type == "announcement") {
        @include anchor-announcement;
    } @else if ($type == "monochrome") {
        @include anchor-monochrome;
    } @else if ($type == "quick-link--feature") {
        @include anchor-quick-link--feature;
    } @else if ($type == "external-img") {
        @include anchor-external-img;
    } @else if ($type == "sliding--left") {
        @include anchor-sliding(left);
    } @else if ($type == "sliding--right") {
        @include anchor-sliding(right);
    } @else if ($type == "primary-navigation") {
        @include anchor-primary-navigation;
    } @else if ($type == "secondary-navigation") {
        @include anchor-secondary-navigation;
    } @else if ($type == "tertiary-navigation") {
        @include anchor-tertiary-navigation;
    } @else if ($type == "video-preview") {
        @include anchor-video-preview;
    } @else if ($type == "skip") {
        @include anchor-skip;
    } @else if ($type == "simple-image") {
        @include anchor-simple-image;
    } @else if ($type == "hallway") {
        @include anchor-hallway($args);
    } @else if ($type == "search-result") {
        @include anchor-search-result;
    } @else if ($type == "skate") {
        @include anchor-skate;
    } @else if ($type == "skate--dark-bg") {
        @include anchor-skate--dark-bg;
    } @else if ($type == "skate--block") {
        @include anchor-skate--block;
    } @else if ($type == "railtrack") {
        @include anchor-railtrack;
    } @else if ($type == "breadcrumb") {
        @include anchor-breadcrumb;
    } @else if ($type == "nav-mobile-link") {
        @include anchor-nav-mobile-link;
    } @else if ($type == "boxed-arrow") {
        @include anchor-boxed-arrow;
    } @else if ($type == "ghost") {
        @include anchor-ghost;
    } @else if ($type == "left-hand-navigation") {
        @include anchor-left-hand-navigation;
    } @else if ($type == "draw-outline") {
        @include anchor-draw-outline($args);
    } @else if ($type == "white") {
        @include anchor-white;
    }
}

@mixin anchor-reset() {
    background-color: inherit;
    color: inherit;
    border-bottom-width: 0;

    &:hover,
    &:focus,
    &:active {
        background-color: inherit;
        color: inherit;
        outline-width: 0;
    }
}

@mixin anchor-block() {
    display: block;

    &:focus {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: $border-width-outline;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: 0;
    }
}

@mixin anchor-block--dark-bg() {
    display: block;
    color: $color-white;

    &:hover,
    &:focus,
    &:active {
        color: $color-gainsboro;
    }

    &:focus {
        outline: $border-width-default $border-style-alternate $color-white;
        outline-offset: $border-width-outline;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-white;
        outline-offset: 0;
    }
}

@mixin anchor-inline-block() {
    display: inline-block;

    &:focus {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: $border-width-outline;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: 0;
    }
}

@mixin anchor-inline() {
    @include transition;
    color: $color-endeavour;
    border-bottom-color: $color-endeavour;
    border-bottom-width: $border-width-default;
    border-bottom-style: $border-style-default;
    text-decoration: none;

    &[href$=".pdf"]:before {
        @include font-family(icon);
        content: "\f1c1";
        padding-right: $measure-small-relative;
        border-bottom-color: $transparent !important;
    }

    &:hover {
        background-color: $color-endeavour;
        color: contrasting-color($color-endeavour);
    }

    &:focus {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: $border-width-outline;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: 0;
    }
}

@mixin anchor-outline() {
    @include transition;

    &:focus {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: $border-width-outline;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: 0;
    }
}

@mixin anchor-outline--dark-bg() {
    @include transition;

    &:focus {
        outline: $border-width-default $border-style-alternate $color-white;
        outline-offset: $border-width-outline;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-white;
        outline-offset: 0;
    }
}

@mixin anchor-dark-background() {
    color: $color-lucifer;
    border-bottom-color: $color-alizarin;

    &:hover {
        background-color: $color-alizarin;
        color: contrasting-color($color-alizarin);
    }

    &:focus,
    &:active {
        outline-color: $color-gallery;
    }
}

@mixin anchor-white() {
    color: $color-white;
    border-bottom-color: $color-white;

    $hover-color: $color-gainsboro;

    &:hover {
        border-color: $hover-color;
        color: $hover-color;
        background-color: transparent;
    }

    &:focus,
    &:active {
        outline-color: $color-white;
    }
}

@mixin anchor-heading() {
    border-bottom-width: $border-width-default;
}

@mixin anchor-card() {
    @include box-shadow(epsilon, $transparent);
    background-color: $color-white;
    color: $color-black;
    display: inline-block;
    border-bottom-width: 0;

    &:hover,
    &:focus,
    &:active {
        @include box-shadow(epsilon, rgba($color-black, 0.75));
        background-color: inherit;
        color: inherit;
        z-index: z-index(above);
    }

    &:focus {
        outline: $border-width-hairline $border-style-alternate $color-black;
    }
}

@mixin anchor-cta() {
    background-color: $color-gallery;
    color: contrasting-color($color-gallery);
    display: block;
    padding: $measure-medium;
    border-bottom-width: 0;
    font-weight: $font-weight-bold;

    &:hover,
    &:focus,
    &:active {
        background-color: $color-quill;
        color: contrasting-color($color-quill);
    }
}

@mixin anchor-cta--small() {
    padding: #{$measure-medium};
}

@mixin anchor-cta--inline() {
    display: inline-block;
    margin-bottom: $measure-small;
}

@mixin anchor-cta--yellow() {
    background-color: $color-yellow;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-yellow, 6%);
        color: $color-black;
    }
}

@mixin anchor-cta--orange() {
    background-color: $color-flamingo;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-flamingo, 6%);
        color: $color-black;
    }
}

@mixin anchor-cta--red() {
    background-color: $color-alizarin;
    color: $color-white;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-alizarin, 6%);
        color: $color-white;
    }
}

@mixin anchor-cta--purple() {
    background-color: $color-vivid;
    color: $color-white;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-vivid, 6%);
        color: $color-white;
    }
}

@mixin anchor-cta--pink() {
    background-color: $color-violet;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-violet, 6%);
        color: $color-black;
    }
}

@mixin anchor-cta--allports() {
    background-color: $color-allports;
    color: contrasting-color($color-allports);

    &:hover,
    &:focus,
    &:active {
        background-color: $color-gallery;
        color: contrasting-color($color-gallery);
    }
}

@mixin anchor-cta--prussian() {
    background-color: $color-prussian;
    color: contrasting-color($color-prussian);

    &:hover,
    &:focus,
    &:active {
        background-color: $color-gallery;
        color: contrasting-color($color-gallery);
    }
}

@mixin anchor-cta--montecarlo() {
    background-color: $color-montecarlo;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-montecarlo, 6%);
        color: $color-black;
    }
}

@mixin anchor-cta--picton() {
    background-color: $color-picton;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-picton, 6%);
        color: $color-black;
    }
}

@mixin anchor-cta--sherpa() {
    background-color: $color-sherpa;
    color: $color-white;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-sherpa, 6%);
        color: $color-white;
    }
}

@mixin anchor-cta--neptune() {
    background-color: $color-neptune;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-neptune, 6%);
        color: $color-black;
    }
}

@mixin anchor-cta--shadow() {
    background-color: transparent;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        background-color: transparent;
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    }
}

@mixin anchor-cta--pita() {
    background-color: $color-pita-blue;
    color: $color-white;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-pita-blue, 6%);
        color: $color-white;
    }
}

@mixin anchor-cta--gtfo() {
    background-color: $color-gtfo-red;
    border: none;
    color: $color-white;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-gtfo-red, 6%);
        border: none;
        color: $color-white;
    }
}

@mixin anchor-cta--hub() {
    background-color: $color-hub;
    border: none;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-hub, 6%);
        border: none;
    }
}

@mixin anchor-cta--hubsecondary() {
    background-color: $color-hubsecondary;
    border: none;
    color: contrasting-color($color-hubsecondary);

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-hubsecondary, 6%);
        border: none;
        color: $color-white;
    }
}

@mixin anchor-cta--hub--borderless() {
    background-color: $color-hub;
    border: none;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        border: none;
        background-color: darken($color-hub, 6%);
    }
}

@mixin anchor-cta--hubgrey() {
    background-color: $color-hubgrey;
    border: none;
    color: $color-black;

    &:hover,
    &:focus,
    &:active {
        background-color: darken($color-hubgrey, 6%);
        border: none;
    }
}

@mixin anchor-left-arrow() {
    &:before {
        @include font-family(icon);
        content: "\f053";
        padding-right: $measure-small-relative;
        border-bottom-color: $transparent !important;
    }
}

@mixin anchor-right-arrow() {
    &:after {
        @include font-family(icon);
        content: "\f054";
        padding-left: $measure-small-relative;
        border-bottom-color: $transparent !important;
    }
}

@mixin anchor-cta--white() {
    background-color: $color-white;
    color: contrasting-color($color-white);

    &:hover,
    &:focus,
    &:active {
        background-color: $color-gallery;
        color: contrasting-color($color-gallery);
    }
}

@mixin anchor-cta--clear-light() {
    background-color: transparent;
    color: $color-white;

    &:hover,
    &:focus,
    &:active {
        background-color: $color-gallery;
        color: contrasting-color($color-gallery);
    }
}

@mixin anchor-purple() {
    @include anchor(inline);
    border: none;
    color: $color-hubsecondary;

    &:hover {
        background-color: $color-hubsecondary;
        border: none;
        color: contrasting-color($color-hubsecondary);
    }
}

@mixin anchor-black() {
    @include anchor(inline);
    color: $color-lucifer;

    &:hover {
