}

@mixin anchor-sliding($direction) {
    display: inline-block;
    background: $color-quill;
    font-weight: $font-weight-bold;

    padding: $measure-small $measure-large;
    color: $color-black;
    border: none;
    overflow: hidden;
    position: relative;

    & > div {
        @include transition(transform);
        transform: translateX(0);
    }

    .url-slide__next {
        @include transition(opacity);
        opacity: opacity(fifth);
        position: absolute;
        white-space: nowrap;
    }

    .url-slide__current {
        @include transition(opacity);
        opacity: 1;
    }

    &:hover,
    &:focus,
    &:active {
        > div {
            transform: translateX(calc(#{3 * $measure-small} - 100%));
        }

        .url-slide__next {
            opacity: 1;
        }

        .url-slide__current {
            opacity: opacity(fifth);
        }
    }
}

@mixin anchor-video-preview() {
