@mixin brackets($color: $color-lucifer) {
    @include anchor(reset);
    color: $color;
    line-height: 1;
    vertical-align: bottom;
    padding: 0 0.7em;
    transition: color 0.2s;
    position: relative;
    display: inline-block;
    margin: 0 3px;
    font-weight: $font-weight-bold;

    a {
        color: $color;
        &:after {
            background-color: transparent;
        }
    }
    &:hover {
        color: $color;
        &:before, &:after {
            opacity: 1;
            transform: translateX(0);
        }
    }
    &:before {
        left: -0.1em;
        content: "[";
        transform: translateX(-100%);
    }
    &:after {
        right: -0.1em;
        content: "]";
        transform: translateX(100%);
    }
    &:before, &:after {
        position: absolute;
        top: -3px;
        font-weight: $font-weight-thin;
        font-size: $font-size-delta;
        line-height: 1;
        opacity: 0;
        transition: opacity 0.2s, transform 0.2s;
    }
}

